            // „Éà„Ç∞„É´„Éú„Çø„É≥Â§âÊõ¥„Ç§„Éô„É≥„Éà
            onChangeEventHandler: function(i){
                // Ë≤∑„ÅÑÁâ©„ÉÅ„Çß„ÉÉ„ÇØ„É™„Çπ„Éà„ÅÆ‰∏≠Ë∫´
                const todos=JSON.parse(window.localStorage.getItem('todos'))||[];
//                const Constfavs=JSON.parse(window.localStorage.getItem('favs'))||[];
                const selectSyouhin = this.nekos[i].name
                
                
                // „Éà„Ç∞„É´ÔºùtrueÔºàË≤∑„ÅÜÔºâ„ÅÆÂ†¥Âêà
                if (this.toggle) {
                    // Ë≤∑„ÅÑÁâ©„ÉÅ„Çß„ÉÉ„ÇØ„É™„Çπ„ÉàÔºàtodosÔºâ„ÅÆ‰∏≠„Å´„Åô„Åß„Å´„ÅäÊ∞ó„Å´ÂÖ•„Çä„Åå„ÅÑ„ÅüÂ†¥Âêà
                    // todos„ÅÆ‰∏≠„Åã„Çâ„ÅäÊ∞ó„Å´ÂÖ•„ÇäÂïÜÂìÅ„ÇíÊ∂à„Åï„Å™„ÅÑ
                    
                    let count = 0;
                    for (let j = 0; j < todos.length; j++) {
                        if (todos[j] === selectSyouhin) {
                            count = 1;
                        }
                    }
                    // todosÂÜÖ„Å´Â≠òÂú®„Åó„Å™„ÅÑÂ†¥Âêà
                    if (count == 0) {
                    
                        // Ë≤∑„ÅÜ„ÇÇ„ÅÆ„É™„Çπ„Éà„Å´ËøΩÂä†
                        todos.push(selectSyouhin);
                        //alert('todos=[' + todos + ']');
                        window.localStorage.setItem('todos',JSON.stringify(todos));
                    }
                    
                    
                // „Éà„Ç∞„É´ÔºùfalseÔºà„ÅÇ„ÇãÔºâ„ÅÆÂ†¥Âêà
                }else{
                    // Ë≤∑„ÅÑÁâ©„ÉÅ„Çß„ÉÉ„ÇØ„É™„Çπ„ÉàÔºàtodosÔºâ„ÅÆ‰∏≠„Å´„Åô„Åß„Å´„ÅäÊ∞ó„Å´ÂÖ•„Çä„Åå„ÅÑ„ÅüÂ†¥Âêà
                    // todos„ÅÆ‰∏≠„Åã„Çâ„ÅäÊ∞ó„Å´ÂÖ•„ÇäÂïÜÂìÅ„ÇíÊ∂à„Åô
                    for (let j = 0; j < todos.length; j++) {
                        if (todos[j] === selectSyouhin) {
                            todos.splice(j, 1)
                            window.localStorage.setItem('todos',JSON.stringify(todos));
                        }
                        
                    }
                }
                // constfavs„Å´Â§âÊõ¥„Åó„Åü„Çπ„ÉÜ„Éº„Çø„Çπ„ÇíË®≠ÂÆö
                this.nekos[i].status = this.toggle
